import React, {Component,Fragment} from 'react'
import ReactDOM from 'react-dom'
import './home.css'
import { If } from 'rc-if-else';

class Home extends Component {
    state ={
        users :[
            {name:"ahmed", age:15, adresse:"tunis", id:Math.random},
            {name:"amir", age:10, adresse:"gafsa", id:Math.random},
            {name:"amin", age:12, adresse:"tozeur", id:Math.random},
            {name:"khalil", age:20, adresse:"tunis", id:Math.random}
        ],
        showInput: false
    }

     
     
    render() {
        let users = this.state.users;
       
        
        const retUsers = users.map( user =>
            {
                return (<div><p className="skills-desc"  key ={Math.random}>Name: {user.name}, age: {user.age}, adresse: {user.adresse}</p>
                        <p>-----------------------------</p>
                        </div>
                    )   
            }
        );

        
        const show = () =>{
            this.setState({
                showInput : true
            })}

        const noShow = () => {
            this.setState({
                showInput : false
            })}

            const addUser = e =>{
                e.preventDefault()
                if(  e.target.name.value !== "" && e.target.age.value !== "" && e.target.adresse.value !== ""){
                    users.push(
                        {name : e.target.name.value,
                        age : e.target.age.value,
                        adresse: e.target.adresse.value})
                    this.setState({
                        users : users
                    })
                    e.target.name.value=""
                    e.target.age.value=""
                    e.target.adresse.value=""
                }
                else {}
                
        }
        return (
            <Fragment>
                <div className="profile_skills">
                    <div className="skills">
                        <h2 className="skills-title">Some <span>skills</span></h2>
                        <p className="skills-desc">
                        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quos praesentium blanditiis esse cupiditate, omnis similique.
                        </p>
                        <div className="bar">
                            <span className="title">Bootstrap</span>
                            <span className="perc">100%</span>
                            <div className="parent">
                                <span className="sp1"></span>
                            </div>
                        </div>
                        
                        <div className="bar">
                            <span className="title">CSS3</span>
                            <span className="perc">90%</span>
                            <div className="parent">
                                <span className="sp2"></span>
                            </div>
                        </div>
                        
                        <div className="bar">
                            <span className="title">Photoshop</span>
                            <span className="perc">80%</span>
                            <div className="parent">
                                <span className="sp3"></span>
                            </div>
                        </div>
                    </div>   
                </div>
                <div className="test2">
                    <h2 >List Of Members </h2>
                    {retUsers}
                    <button type="button" className="btn btn-danger" onClick ={show} onMouseEnter={noShow}>Add User</button>
                    <If condition={this.state.showInput} >
                        <form onSubmit={addUser}>
                            <input className = "set" type="text" name = "name" id = "name" placeholder ="name"  />
                            <input className = "set" type="text" name = "age" id = "age" placeholder ="age" /> 
                            <input className = "set" type="text" name = "adresse" id = "adresse" placeholder ="adresse" />   <input type="submit" value="Submit" />
                        </form>
                    </If>
                </div>
            </Fragment>
        )
    }
}

export default Home